import React, {
	useEffect,
	useRef,
	useState,
	forwardRef,
	useImperativeHandle,
} from "react";


import UseDelayInput from "../hooks/useDelayInput";
import { updStore, gen16CodeId } from "../stores/Utils";

import "./DropInput.css";


const DropInputComponent = forwardRef( ({ fetchData, loading, width, keyFlds, callempty,placeholder, change, children },ref) => {
	const [searchQr, setSearchQ] = useState("");
	const searchRef = useRef();
	const [open, setOpen] = useState(false);
	const [show, setShow] = useState(false);
	let deferredInput = UseDelayInput(searchQr);

	useEffect(() => {
		if (!deferredInput) {
			updStore(keyFlds, "");
			if(show){
				setShow(false)
				setTimeout(() => {
					setOpen(false)
				}, 200);
			}
			callempty && callempty()
		} else {
			updStore(keyFlds, deferredInput);
			fetchData && fetchData(deferredInput);
			if(!show){
				setOpen(true)
				setTimeout(() => {
					setShow(true)
				}, 50);
			}
		}
		return () => {
			//;
		};
	}, [deferredInput]);



	const handleInput = (v) => {
		setSearchQ(v.target.value);
		if (typeof change === "function") {
			change(v.target.value);
		}
	};

	useImperativeHandle(ref, () => ({
		clearTx() {
			setSearchQ("");
			if(show){
				setShow(false)
				setTimeout(() => {
					setOpen(false)
				}, 200);
			}
		},
		initTx(v) {
			setSearchQ(v);
			if (typeof change === "function") {
				change(v);
			}
		}
	}));

	let styleOpen = {
		position: "absolute",
		minHeight: 265,
		width: "100%",
		background: "#fff",
		transition: "opacity easy"
	}

	return (
		<div style={{position:"relative"}}>

			<input
                style={width? {width: width}: null}
				ref={searchRef}
				className="r7gAOb "
				type="text"
				//aria-autocomplete="both"
				aria-haspopup="false"
				auto-capitalize="off"
				auto-complete="off"
				auto-correct="off"
				spell-check="false"
				placeholder={placeholder || "buscar"}
				onChange={handleInput}
			/>
			{loading ?
				<div className="load_animation"/>
			 :<div style={{height:2}}/>}
			{open?
			 <div className={"boxOpenContainer"} style={{opacity:show?1:0}}>
				{children}
			</div>

			:null}
		</div>
		
	);
});

export default DropInputComponent;
