import React, { useEffect, useState } from "react";

const UseDelayInput = (input) => {
	const [timeStamp, setTimeStamp] = useState(null);
	const [value, setValue] = useState("");
	const [tOut, setTOut] = useState(null);
	const DELAY = 220;
	useEffect(() => {
		if (!timeStamp) {
			setValue(input);
		}
		if (!(timeStamp > 0 && timeStamp < Date.now())) {
			tOut && clearTimeout(tOut);
		}
		let timeOutI = setTimeout(() => {
			if (timeStamp > 0 && timeStamp < Date.now()) {
				if (input !== value) {
					setValue(input);
				}
			}
		}, DELAY + 30);
		setTOut(timeOutI);
		setTimeStamp(Date.now() + DELAY);
		return () => {
			//
		};
	}, [input]);

	return value;
};

export default UseDelayInput;
